:root{--body-color: #6A8B99;--white: white;--primary-color: #1F2944;--orange-color: #F6A800}html{box-sizing:border-box;margin:0;padding:0}*{margin:0;padding:0;box-sizing:border-box;scroll-behavior:smooth}body.page{background-color:var(--body-color);color:var(--primary-color);font-family:"Poppins",sans-serif !important;font-weight:400;font-kerning:none;line-height:1.5;font-size:14px}body:not(.blog) #genesis-content p a,body:not(.blog) #genesis-content ul li a{color:var(--orange-color);text-decoration:underline}a{text-decoration:none;transition:.1s linear}a:focus,a:hover{color:#333;text-decoration:none}p{margin:0 0 28px;padding:0;overflow-wrap:normal;word-break:normal}ol,ul{margin:0;padding:0}li{list-style-type:none;font-size:1rem}b,strong{font-weight:700}.elementor div.slide-1{background-color:var(--body-color);padding:0}.slide-1 .e-con-inner{margin:0;padding:3vh 4vw !important;max-width:100% !important}.slide-1 .e-con-inner .elementor-element{padding:0 !important;margin-left:.2vw}.slide-1-column-1{width:65% !important}.slide-1 .e-con-inner .slide-1-column-2{align-items:flex-start}.slide-1-image .elementor-widget-container img{height:113vh;width:auto}.slide-1-title h2{font-weight:500 !important;font-size:4.2rem;line-height:4rem;margin-top:17vh}.slide-1-column-1 .elementor-widget-container p{font-size:1.5rem;line-height:1.5rem;margin:0}.slide-1-span-1 .elementor-widget-container p{color:var(--white);font-weight:400;margin-top:9vh}.slide-1-span-2 .elementor-widget-container p{color:var(--primary-color);font-weight:900;font-style:italic}.elementor div.slide-2{background-color:var(--primary-color);padding:0;height:551px}.slide-2 .e-con-inner .slide-2-column-1{padding:0;width:47% !important;align-items:flex-end}.slide-2 .e-con-inner .slide-2-column-2{padding:0;align-items:flex-start}.elementor-element div.slide-2-title{color:#fff;font-size:1.6rem;letter-spacing:.2rem;padding-top:8vh}span.brent-name{color:var(--orange-color)}.elementor-element div.slide-2-subtitle{color:#fff;font-size:1.3rem;font-style:italic;letter-spacing:.1rem;padding-top:1vh}.elementor-element div.slide-2-text{color:#fff;font-size:1.5rem;width:72%;line-height:1.9rem;padding-top:1.5vh}.slide-2-image div img{position:relative;bottom:30.4vh;height:125vh;width:auto}